/* 컨테이너 스타일 */
.projectContainer {
    padding: 20px;
    background-color: #f9f9f9;
    font-family: Arial, sans-serif;
}

.projectContent {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

/* 제목 스타일 */
.projectHeading {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
}

/* 리스트 스타일 */
.projectList {
    list-style: none;
    padding: 0;
    margin-bottom: 30px;
    font-size: 16px;
    color: #555;
}

.projectList li {
    margin-bottom: 10px;
}

/* 섹션 스타일 */
.projectGallery {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
}

.projectItem {
    width: 45%;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.projectItem:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
}

/* 이미지와 캡션 스타일 */
.projectItem img {
    width: 100%;
    height: auto;
    display: block;
}

.projectItem figcaption {
    padding: 10px;
    font-size: 16px;
    color: #007BFF;
    text-align: center;
}

.projectItem figcaption a {
    text-decoration: none;
    color: inherit;
}

.projectItem figcaption a:hover {
    text-decoration: underline;
}

/* 반응형 스타일 */
@media (max-width: 768px) {
    .projectGallery {
        flex-direction: column;
        align-items: center;
    }

    .projectItem {
        width: 100%;
    }
}