@charset "utf-8";

/* 어사이드 전체 */
.aside {
    z-index: 1000;
    position: fixed;
    margin: 10px;
    top: 100px;
    padding: 20px;
    right: -200px;
    width: 220px;
    height: 480px;
    background: linear-gradient(90deg, rgba(165, 182, 250, 1) 0%, rgba(82, 119, 239, 1) 100%);
    border-radius: 10px;
    box-sizing: border-box;
    transition: right 0.5s ease-in-out;
    /* padding-inline-start: 0px */
}

.asidenav ul {
    list-style-type: none;
}

.aside .asidenav ul {
    padding: 0;
    margin-right: 0px;
    margin-bottom: 10px;
    text-align: center;
    /* 글자 왼쪽 정렬 */
}

/* 내비게이션 영역 */
.aside .asidenav {
    margin-top: 20px;
}

.aside .asidenav a {
    font-weight: bold;
    display: block;
    width: 180px;
    height: 36px;
    border: 0px solid #ddd;
    margin-bottom: 10px;
    padding: 0px;
    text-align: center;
    line-height: 36px;
    color: #ffffff;
    text-decoration: none;
}

.aside .asidenav a:hover {
    background: pink;
    color: #000000;
    border-radius: 10px;
}

/* 어사이드 상단 이미지 */
.asideImg {
    width: 180px;
    height: 180px;
    border-radius: 10px;
}

/* 토글 버튼 */
.asideToggle {
    z-index: 9999;
    position: absolute;
    top: 150px;
    left: -55px;
    /* -52px 말고 -50px이 자연스러움 */
    width: 50px;
    height: 50px;
    background-color: pink;
    border: none;
    border-radius: 10px;
    padding: 0;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
    /* 부드러운 애니메이션 추가 */
}

.asideToggle:hover {
    transform: scale(1.1);
}

/* 버튼 이미지 */
.asideToggle img {
    width: 70%;
    height: 70%;
    display: block;
    object-fit: contain;
    /* 다시 contain으로! */
    transition: all 0.3s ease;
    /* 부드러운 애니메이션 추가 */
}