

.mapContainer {
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px;
    text-align: center;
    background-color: #f8f9fa;
    min-height: 100vh;
}

.title {
    color: #2c3e50;
    margin-bottom: 40px;
    font-size: 2.5rem;
    font-weight: 700;
    position: relative;
    display: inline-block;
    padding-bottom: 10px;
}

.title::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 60%;
    height: 3px;
    background: linear-gradient(90deg, #3498db, #2ecc71);
    border-radius: 2px;
}

.mediaWrapper {
    margin-bottom: 70px;
    padding: 20px;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.mediaWrapper:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.iframe {
    max-width: 100%;
    border-radius: 10px;
    transition: transform 0.3s ease;
    aspect-ratio: 16/9;
    width: 100%;
    height: auto;
    min-height: 400px;
}

@media (max-width: 768px) {
    .mapContainer {
        padding: 20px 10px;
    }

    .title {
        font-size: 2rem;
        margin-bottom: 30px;
    }

    .mediaWrapper {
        margin-bottom: 50px;
        padding: 15px;
    }

    .iframe {
        min-height: 300px;
    }
}