@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap');

.TopNav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Nanum Gothic', sans-serif;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    background-color: #000;
    color: #fff;
    margin: 0px;
    padding: 5px 250px;
    gap: 10px;
}

.menuContainer {
    display: flex;
    gap: 2rem;
    align-items: center;
}

.TopNav select {
    padding: auto;
    margin: auto;
    font-size: 16px;
}

.translateContainer {
    margin-left: auto;
}

.link {
    text-decoration: none;
    color: #fff;
    padding: 0.5rem;
    transition: color 0.3s ease;
}

.link:hover {
    color: #0b9595;
    text-decoration: underline 3px;
}

.select {
    padding: 0.5rem;
    border-radius: 4px;
    border: 1px solid #333;
    background-color: #000;
    color: #fff;
}

.select option {
    background-color: #000;
    color: #fff;
}

/* 기본: PC 화면 */
.menu-select {
    display: none;
    /* 드롭다운 숨기기 */
}

/* 구글 번역 박스 모바일에서 왼쪽으로 고정 */
@media (max-width: 768px) {
    .TopNav #google_translate_element {
        position: absolute;
        /* 기존 margin을 없애고 절대 위치로 조정 */
        margin: 0;
        left: 20px;
        /* 상단에서 조금 떨어지게 위치 */
        top: 5px;
        display: none;
    }

    /* 모바일에서 메뉴를 드롭다운으로 변경 */
    .menu-select {
        display: block;
        /* 드롭다운을 보이게 */
    }

    .TopNav {
        padding: 5px 0px;
        margin-right: 0 auto;
        /* 모바일에서는 패딩을 줄여서 공간을 확보 */
    }

    /* 링크 스타일을 모바일에서 좀 더 간단하게 조정 */
    .TopNav .link {
        font-size: 14px;
        margin: 0 10px;
    }
}

/* 기본: PC 화면 */
@media (max-width: 768px) {
    .TopNav #google_translate_element {
        position: static;
        /* 구글 번역을 원래 위치로 되돌림 */
        margin: 10px auto;
        /* 가운데 정렬 */
    }

    .MenuSelect {
        display: none;
        /* 드롭다운 숨기기 */
    }
}

