.textbookContainer {
  max-width: 1400px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #ffffff;
  min-height: 90vh;
}

.header {
  text-align: center;
  margin-bottom: 3rem;
}

.title {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 600;
}

.subtitle {
  font-size: 1.2rem;
  color: #666;
}

.bookGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem;
  padding: 0px;
  max-width: 1200px;
  margin: 0 auto;
  background: #f8f9fa;
  border-radius: 20px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
}

/* 선반 효과 */
.bookGrid::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  height: 15px;
  background: linear-gradient(90deg, 
    rgba(160, 160, 160, 0.5),
    rgba(200, 200, 200, 0.8) 20%,
    rgba(220, 220, 220, 0.9) 50%,
    rgba(200, 200, 200, 0.8) 80%,
    rgba(160, 160, 160, 0.5)
  );
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  bottom: -15px;
  border-radius: 0 0 5px 5px;
}

.bookCard {
  background: white;
  border-radius: 15px;
  padding: 0px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.bookCard::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 30%;
  background: linear-gradient;
  border-radius: 10px 10px 0 0;
  pointer-events: none;
}

.bookCard:hover {
  /* transform: translateY(-5px); */
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

.bookImage {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.bookInfo {
  padding: 1.5rem;
}

.bookTitle {
  font-size: 1.25rem;
  font-weight: 600;
  color: #2c3e50;
  margin: 1rem 0;
  text-align: center;
}

.bookDescription {
  font-size: 0.9rem;
  color: #666;
  line-height: 1.5;
  margin-bottom: 1rem;
}

.bookMeta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.85rem;
  color: #888;
}

.button {
  background-color: #4a90e2;
  color: white;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.button:hover {
  background-color: #357abd;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .bookGrid {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

@media (max-width: 768px) {
  .bookGrid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .title {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .bookGrid {
    grid-template-columns: 1fr;
  }
  
  .title {
    font-size: 1.75rem;
  }
}
