.TextHelperHtml {
    font-family: Arial, sans-serif;
    background-color: #f0f8ff; /* 연한 하늘색 배경 */
    padding: 0;
    margin: 0;
    color: #333;
}

.TextHelperHeader {
    background-color: #4a90e2; /* 하늘색 헤더 */
    color: white;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.TextHelperHeader h1 {
    font-family: 'Jua', sans-serif;
    font-size: 30px;
    font-weight: 200;
    text-align: center;
    color: #ffffff;
    margin: 15px 0 20px 0;
    padding: 0;
    line-height: 1;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(2px);
}

.TextHelperBody {
    margin-top: 20px;
    display: flex;
    flex-direction: row; /* 좌우 배치 */
    justify-content: space-between;
    gap: 20px;
    padding: 0 40px; /* 좌우 여백을 40px로 설정 (CodingHelper와 동일) */
}

.TextHelperDescription {
    height: 100%;
}
.TextHelperLeft, .TextHelperRight {
    flex: 1;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    background-color: #e8f4ff; /* 연한 하늘색 */
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin: 20px;
    height: 80%;
    height: 400px;
}

.TextHelperRight {
    background-color: #d4e9ff; /* 조금 더 진한 하늘색 */
}

.TextHelperLeft h2,
.TextHelperRight h2 {
    color: #2c5282; /* 진한 하늘색 */
    margin-bottom: 10px;
}

.TextHelperLeft p,
.TextHelperRight p {
    margin: 5px 0;
    line-height: 1.6;
}

.TextHelperImage {
    max-width: 100%;
    height: auto;
    border-radius: 0px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
}

.TextHelperContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    width: 100%;
    max-width: 1200px; /* 최대 너비 제한 */
    margin: 0 auto; /* 가운데 정렬 */
    padding: 0 40px; /* 좌우 여백 추가 */
}

.TextHelperVersionContainer {
    background-color: #ffe0b2; /* 연한 주황색 박스 */
    padding: 20px;
    border-radius: 0px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 600px;
}

.TextHelperVersionBox {
    text-align: center;
}

.TextHelperVersionTitle {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #2c5282; /* 진한 하늘색 */
}

.TextHelperVersionBox a {
    display: inline-block;
    margin-top: 10px;
    text-decoration: none;
    color: white;
    background-color: #4a90e2; /* 하늘색 버튼 */
    padding: 10px 20px;
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s;
}

.TextHelperVersionBox a:hover {
    background-color: #2c5282; /* 버튼 호버 시 진한 하늘색 */
}

.TextHelperVersionImg {
    max-width: 80%; /* 이미지 크기를 줄임 */
    height: auto;
    border-radius: 0px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
}

.TextHelperText {
    background-color: #fff3e0; /* 더 연한 주황색 */
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 600px;
}

.TextHelperText h2 {
    color: #2c5282; /* 진한 하늘색 */
    margin-bottom: 10px;
}

.TextHelperText p {
    margin: 5px 0;
    line-height: 1.6;
}

.TextHelperTable {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
}

.TextHelperTable th, .TextHelperTable td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: left;
}

.TextHelperTable th {
    background-color: #f4f4f4;
    font-weight: bold;
}

.TextHelperTable td {
    vertical-align: top;
}

.TextHelperTable tr:nth-child(even) {
    background-color: #f9f9f9;
}

.TextHelperTable td:first-child {
    font-weight: bold;
    width: 40%;
}

.TextHelperSettings {
    list-style-type: disc;
    margin-left: 20px;
}

.TextHelperContent {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-top: 20px;
}

.DownloadButton {
    display: inline-block;
    margin-top: 15px;
    margin: 0 auto;
    text-decoration: none;
    color: white;
    background-color: #4a90e2; /* 하늘색 버튼 */
    padding: 10px 20px;
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s;
}

.DownloadButton:hover {
    background-color: #2c5282; /* 버튼 호버 시 진한 하늘색 */
}

.TextHelperDescription {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-top: 20px;
  width: 100%;
}
 
@media (max-width: 768px) {
    .TextHelperBody {
        flex-direction: column; /* 모바일에서는 세로 배치 */
    }

    .TextHelperContent {
        flex-direction: column; /* 세로 배치 */
        gap: 10px; /* 간격 줄이기 */
    }
    .TextHelperVersionImg {
        max-width: 100%; /* 이미지 크기 조정 */
        color: #2c5282; /* 진한 하늘색 */
    }
    .TextHelperList strong {
        margin-bottom: 10px;
    }
    .TextHelperList li {
        color: #333;
        line-height: 1.6;
        padding: 0;
        margin: 10px 0 0 20px;
        list-style-type: disc;
    }
    .TextHelperList {
    list-style-type: disc;
    margin: 10px 0 0 20px;
    padding: 0;
    line-height: 1.6;
    color: #333;
    }
    .TextHelperList li {
    margin-bottom: 10px;
    }
    .TextHelperList strong {
    color: #2c5282; /* 진한 하늘색 */
    }
}